/*=======  COMMON CSS  =======*/

:root {
  --thm-font-family: "Source Sans Pro", sans-serif;
  --thm-primary: #0096c7;
  --thm-secondary: #14212b;
  --thm-white: #fff;
  --thm-whitesmoke: #f5f5f5;
  /* --thm-primary: #ff4a17; */
  --thm-text: #616161;
}

body {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #616161;
  color: var(--thm-text);
  overflow-x: hidden;
  font-size: 18px;
  line-height: 1.8;
}

@media (max-width: 575px) {
  body {
    font-size: 16px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  transition: all 0.4s ease-out 0s;
}

a:focus,
input:focus,
textarea:focus,
button:focus,
.slick-initialized .slick-slide:focus,
.btn:focus,
select:focus {
  text-decoration: none;
  outline: none;
  box-shadow: none;
}

input,
textarea,
button,
select {
  border: none;
}

input,
textarea,
select {
  width: 100%;
  background-color: #fff;
  background-color: var(--thm-white);
  padding: 0 25px;
  height: 70px;
  font-weight: 600;
  font-size: 15px;
  color: #616161;
  color: var(--thm-text);
}

textarea {
  height: 140px;
  padding-top: 20px;
  resize: none;
}

::-webkit-input-placeholder {
  opacity: 1;
}

::placeholder {
  opacity: 1;
}

::-webkit-scrollbar {
  background-color: #ccc;
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: #0096c7;
  background-color: var(--thm-primary);
}

.input-group {
  position: relative;
}

.input-group input,
.input-group textarea,
.input-group select {
  border: 1px solid transparent;
  padding-right: 50px;
}

.input-group input:focus,
.input-group textarea:focus,
.input-group select:focus {
  border: 1px solid #0096c7;
  border: 1px solid var(--thm-primary);
}

.input-group select {
  -webkit-appearance: none;
  -moz-appearance: none;
}

.input-group select::-ms-expand {
  display: none;
}

.input-group .icon {
  position: absolute;
  right: 25px;
  font-size: 14px;
  color: #0096c7;
  color: var(--thm-primary);
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.input-group.textarea .icon {
  top: 20px;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.slick-slide img {
  display: inline-block;
}

a:focus,
a:hover {
  text-decoration: none;
}

i,
span,
a {
  display: inline-block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Source Sans Pro", sans-serif;
  font-family: var(--thm-font-family);
  font-weight: 600;
  margin: 0px;
}

h1,
h1 a,
h2,
h2 a,
h3,
h3 a,
h4,
h4 a,
h5,
h5 a,
h6,
h6 a {
  color: #14212b;
  color: var(--thm-secondary);
}

h1 {
  font-size: 48px;
}

h2 {
  font-size: 36px;
}

h3 {
  font-size: 28px;
